// General Imports:
import React, { useEffect } from 'react';
import Aos from 'aos';

// Components:
import { AboutInfo } from './AboutInfo';
import FvAstro from '../../img/FvAstro.svg';

// Styles:
import '../../styles/About.css'
import '../../styles/animation.css';
import 'aos/dist/aos.css';


export const About = () => {

    // Scroll Animation:
    useEffect(() => {
        Aos.init({
            duration: 1500,
            easing: 'ease'
        });
    }, [])

    return (
        //General Container:
        <section className='about_container'>

            {/* About Container: */}
            <div className='about'>

                <AboutInfo />
                <img 
                alt='fvc'
                src={FvAstro} 
                className='about-img'
                data-aos='fade-up'
                />
                
            </div>

        </section>
    );
}
