import React, {useState} from 'react';
import {FaAngleDown} from 'react-icons/fa';

export const ProjectCard03 = () => {

  const [description, setDescription] = useState(false);
  const toggleClass = () => {
    setDescription(!description)
  };

  return (
    // Project Card:
      <div className='project-card-box box-tree'>

        <span className='project-card-content'>
          <h5 className='project-card-content-title'> WORDPRESS WEBSITE </h5>
          <p className='project-card-content-desc'>CMS WORDPRESS</p>
          <button 
          className={description ? 'button-rotate' : 'project-card-button'}
          onClick={toggleClass}
          >
            <FaAngleDown/>
          </button>
        </span>

        {/* Project Description */}
        <span className={description ? 'project-card-description-active' : 'project-card-description'}>
          <p className='project-card-description-desc'>
            Website Developed in Wordpress
          </p>
            <a 
            target="blank" 
            href='https://fahrestaurant.com/'
            className='project-card-btn'
            >
              Check Out
            </a>
        </span>

      </div>
  )
}
