import React from 'react';
import { Link } from 'react-scroll';
import Triangle from '../../img/TriangleLogo.svg';

export const DesktopMenu = () => {
    return (
        <ul className='menu_container' >

            <li>
                <Link 
                    to='about'
                    className='menu_container_link'
                    smooth={true}
                    duration={1000}>
                    ABOUT
                </Link>
            </li>

            <li>
                <Link 
                    to='skills_container'
                    className='menu_container_link'
                    smooth={true}
                    duration={1000}>
                    SKILLS
                </Link>
            </li>

            <Link
                to='home_container'
                smooth={true}
                duration={1000}
                className='triangle-logo'
                >
                <img
                    src={Triangle}
                    alt='TriangleLogo'
                    >
                </img>
            </Link>

            <li>
                <Link  
                    to='portfolio_container'
                    className='menu_container_link'
                    smooth={true}
                    duration={1000}>
                    PORTFOLIO
                </Link>
            </li>

            <li>
                <Link 
                    to='contact_container'
                    className='menu_container_link'
                    smooth={true}
                    duration={1000}>
                    CONTACT
                </Link>
            </li>

        </ul>
    )
}
