// General Imports:
import React, { useEffect } from 'react';
import Aos from 'aos';

// Styles:
import '../../styles/ValueProposal.css';
import '../../styles/animation.css';
import 'aos/dist/aos.css';

export const ValueProposal = () => {

  // Scroll Animation:
  useEffect(() => {
    Aos.init({
      duration: 1500,
      easing: 'ease'
    });
  }, [])

  return (

    <div className='value-container'>

      <h3
        className='value-text text-one'
        data-aos='fade-right'
      >
        “Helping people to turn ideas into beautiful sites”
      </h3>

      <h3
        className='value-text'
        data-aos='fade-left'
      >
        "I find solutions that elevate human experiences"
      </h3>

    </div>

  )
}
