// General Imports:
import React, { useState, useEffect } from 'react';

// Img Sources
import FloatingPlanet from '../../img/FloatingPlanet.svg';
import Astronaut from '../../img/Astronaut.svg';
import Clouds from '../../img/Clouds.png';


// Styles:
import '../../styles/Home.css';

export const Home = () => {

    // Parallax Effect:
    const [offset, setOffset] = useState(0);
    const handleScroll = () => setOffset(window.pageYOffset);

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [])

    return (

        // Header Home Parallax Container:
        <section className='home_container'>

            {/* Stars */}
            <div className='star-box'>
                <span className=' star'></span>
                <span className='star'></span>
                <span className='star'></span>
            </div>

            {/* Title */}
            <h2
                className='header_text'
                style={{ transform: `translateY(${offset * 0.5}px )` }}
            >
                IMAGINATION<br />
                BEYOND LIMITS<br />
            </h2>

            {/* Floating Planet */}
            <img
                src={FloatingPlanet}
                alt='Planet'
                className='floating-planet'
                style={{ transform: `translateY(${offset * 0.6}px )` }}
            />

            {/* Astronaut */}
            <img
                src={Astronaut}
                alt='Astronaut'
                className='astronaut'
                style={{ transform: `translateY(${offset * 0.4}px )` }}
            />

            {/* Clouds */}
            <img
                src={Clouds}
                alt='Clouds'
                className='clouds'
                style={{ transform: `translateY(${offset * 0.25}px )` }}
            />

        </section>
    );

}
