import React, { useEffect } from 'react';
import Aos from 'aos';

// Styles:
import '../../styles/Portfolio.css';
import '../../styles/animation.css';
import 'aos/dist/aos.css';
import { ProjectCard01 } from './ProjectCard01';
import { ProjectCard02 } from './ProjectCard02';
import { ProjectCard03 } from './ProjectCard03';

export const Portfolio = () => {

    // Scroll Animation:
    useEffect(() => {
        Aos.init({
            duration: 1500,
            easing: 'ease'
        });
    }, [])

    return (
        <div className='portfolio_container'>

            {/* Sample Projects Tittle */}
            <h2
                className='portfolio_tittle'
                data-aos='fade-right'>
                Sample Projects
                <hr
                    className='portfolio_tittle_line'
                    data-aos='fade-left'
                />
            </h2>

            {/* Skill Project Container */}
            <div className='portfolio_project' data-aos='fade-up'>

                <ProjectCard01/>
                <ProjectCard02/>
                <ProjectCard03/>

            </div>

        </div>
    )
}
