//  General Imports:
import React, { useEffect } from 'react';
import { Link } from 'react-scroll';
import Aos from 'aos';
import AstroMini from '../../img/AstroMini.svg';


// Styles:
import '../../styles/HireMe.css';
import '../../styles/animation.css';

export const HireMe = () => {

    // Scroll Animation:
    useEffect(() => {
        Aos.init({
            duration: 1500,
            easing: 'ease'
        });
    }, [])

    return (

        // HireMe Section Container:
        <div className='hireme'>

            {/* HireMe General Info */}
            <div
                className='hireme_container'>

                <img
                    src={AstroMini}
                    alt='AstroMini'
                    className='astro_mini'
                    data-aos='fade-left'
                />
                <h2
                    className='hireme_tittle'
                    data-aos='fade-up'
                >
                    Do you have a project in mind?
                </h2>

            </div>

            <div data-aos='fade-up'>
                <Link
                    to='contact_container'
                    className='hireme_button'
                    smooth={true}
                    duration={1000}>
                    Contact Me
                </Link>
            </div>

        </div>

    );
}
