// General Imports:
import React from 'react';

// Styles:
import '../../styles/Footer.css';
import '../../styles/animation.css';
import LinkedIn from '../../img/icons/linkedin.svg';
import Instagram from '../../img/icons/instagram.svg';
import Github from '../../img/icons/githubMini.svg';

export const Footer = () => {
    return (

        // General Container:
        <footer className='footer'>

            {/* Footer Copy Right */}
            <div className='footer_cr'>
                <p>Copyright &copy; 2022 AzafWebs</p>
            </div>

            {/* Footer Icons */}
            <div className='footer_icon_container'>

                {/* Footer Top Text */}
                <h5
                    className='footer_text'>
                    Follow Me
                </h5>

                <a 
                href='https://www.instagram.com/fer_azaf'
                target="blank"
                >
                    <img
                        src={ Instagram }
                        alt='Instagram'
                        className='footer_social'
                    />
                </a>
                <a 
                href='https://www.linkedin.com/in/fernando-ventura-c' 
                target="blank"
                >
                    <img
                        src={ LinkedIn }
                        alt='LinkedIn'
                        className='footer_social'
                        
                    />
                </a>
                <a 
                href='https://github.com/FerVenc'
                target="blank"
                >
                    <img
                        src={ Github }
                        alt='Github'
                        className='footer_social'
                    />
                </a>
            </div>

        </footer>

    );
}
